<template>
    <div>
        <v-dialog fullscreen v-model="activate">
            <v-card>
                <v-card-title>
                    <span class="headline">Ordem</span>
                </v-card-title>

                <v-divider></v-divider>

                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="9">
                                <v-text-field
                                    readonly
                                    v-model="customer.name"
                                    label="Nome do cliente"
                                    @click="dialogCustomers = true"
                                ></v-text-field>
                            </v-col>
                            <v-spacer></v-spacer>
                            <v-col cols="2">
                                <v-btn
                                    text
                                    color="primary"
                                    @click="dialogCustomers = true"
                                >
                                    Clientes
                                </v-btn>
                            </v-col>
                        </v-row>
                        <v-row>
                            <!-- combo tipo -->

                            <v-col cols="12" sm="4" md="3">
                                <v-select
                                    v-model="localOrder.type"
                                    :items="types"
                                    label="Tipo"
                                ></v-select>
                            </v-col>
                            <!-- TextField Titulo -->

                            <v-col cols="12" sm="5" md="5">
                                <v-text-field
                                    v-model="localOrder.title"
                                    label="Nome do processo"
                                ></v-text-field>
                            </v-col>
                            <!-- TextField protocolo -->

                            <v-col cols="12" sm="3" md="4">
                                <v-text-field
                                    v-model="localOrder.protocol"
                                    label="Protocolo"
                                ></v-text-field>
                            </v-col>

                            <!--date picker criação -->

                            <v-col cols="12" sm="6" md="4">
                                <v-menu
                                    ref="modalCreation"
                                    v-model="modalCreation"
                                    :close-on-content-click="false"
                                    :return-value.sync="localOrder.creation"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-text-field
                                            v-model="computedCreationFormatted"
                                            label="Data de Criação"
                                            prepend-icon="mdi-calendar"
                                            readonly
                                            v-bind="attrs"
                                            v-on="on"
                                        ></v-text-field>
                                    </template>
                                    <v-date-picker
                                        v-model="localOrder.creation"
                                        locale="pt-br"
                                        scrollable
                                    >
                                        <v-spacer></v-spacer>
                                        <v-btn
                                            text
                                            color="primary"
                                            @click="modalCreation = false"
                                        >
                                            Cancela
                                        </v-btn>
                                        <v-btn
                                            text
                                            color="primary"
                                            @click="
                                                $refs.modalCreation.save(
                                                    localOrder.creation
                                                )
                                            "
                                        >
                                            OK
                                        </v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>

                            <!-- Combo situação -->

                            <v-col cols="12" sm="6" md="4">
                                <v-select
                                    v-model="localOrder.status"
                                    :items="statusList"
                                    label="Situação"
                                    @change="onStatusChange"
                                ></v-select>
                            </v-col>

                            <!--date picker conclusão -->

                            <v-col cols="12" sm="6" md="4">
                                <v-menu
                                    ref="modalConclusion"
                                    v-model="modalConclusion"
                                    :close-on-content-click="false"
                                    :return-value.sync="localOrder.conclusion"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-text-field
                                            v-model="
                                                computedConclusionFormatted
                                            "
                                            label="Data de Conclusão"
                                            prepend-icon="mdi-calendar"
                                            readonly
                                            v-bind="attrs"
                                            v-on="on"
                                        ></v-text-field>
                                    </template>
                                    <v-date-picker
                                        v-model="localOrder.conclusion"
                                        locale="pt-br"
                                        scrollable
                                    >
                                        <v-spacer></v-spacer>

                                        <v-btn
                                            text
                                            color="warning"
                                            @click="clearConclusionDate()"
                                        >
                                            limpa
                                        </v-btn>

                                        <v-btn
                                            text
                                            color="primary"
                                            @click="modalConclusion = false"
                                        >
                                            Cancela
                                        </v-btn>
                                        <v-btn
                                            text
                                            color="primary"
                                            @click="
                                                $refs.modalConclusion.save(
                                                    localOrder.conclusion
                                                )
                                            "
                                        >
                                            OK
                                        </v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>

                            <!-- TextField Preço -->

                            <v-col cols="12" sm="4" md="4">
                                <v-currency-field
                                    label="Preço"
                                    prefix="R$"
                                    :error-messages="errors.price"
                                    v-model="localOrder.price"
                                    :readonly="
                                        localOrder.billingStatus != 'Aberto'
                                    "
                                />
                            </v-col>

                            <!-- Combo situação -->

                            <v-col cols="12" sm="4" md="4">
                                <v-text-field
                                    v-model="localOrder.billingStatusToShow"
                                    label="Faturamento"
                                    readonly
                                ></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="save"
                        >Salvar</v-btn
                    >

                    <v-btn color="blue darken-1" text @click="close"
                        >Cancelar</v-btn
                    >
                </v-card-actions>
            </v-card>
        </v-dialog>
        <!-- clientes -->
        <list-customers
            v-bind:customer.sync="customer"
            v-bind:activate.sync="dialogCustomers"
        ></list-customers>
        <v-snackbar
            v-model="snackAlert"
            
            >
            {{ alertText }}

            <template v-slot:action="{ attrs }">
                <v-btn
                color="indigo"
                text
                v-bind="attrs"
                @click="snackAlert = false"
                >
                OK
                </v-btn>
            </template>
            </v-snackbar>
    </div>
</template>

<script>
import firebase from "firebase";
import moment from "moment";
import Order from "../../forms/order.model";
import { getValue } from "vue-currency-input";
import ListCustomers from "./ListCustomers.vue";
import CustomerService from "../services/customer-service";
export default {
    components: { ListCustomers },
    name: "Order",

    props: {
        order: Object,
        activate: Boolean,
    },

    data: () => ({
        rate: 0,
        dialogCustomers: false,
        customer: Object(),
        alertText:'',
        snackAlert: false,
        errors: {},
        config() {
            return {
                mask: "['###.###.###-##', '##.###.###/####-##']",
                tokens: "123456",
                masked: true,
            };
        },

        types: ["Constituição", "Transformação", "Extinção", "Cisão"],
        statusList: ["Aberto", "A. Doc", "Andamento", "Final"],
        modalCreation: false,
        modalConclusion: false,

        localOrder: new Order(),

        saveEnabled: false,
        value: "0",
        numberValue: null,
    }),

    computed: {
        computedCreationFormatted() {
            return this.formatDate(this.localOrder.creation);
        },
        computedConclusionFormatted() {
            return this.formatDate(this.localOrder.conclusion);
        },
    },

    watch: {
        value() {
            this.numberValue = getValue(this.$refs.input);
        },

        order: function (val) {
            if (val.creation) this.localOrder.creation = val.creation;
            if (val.conclusion) this.localOrder.conclusion = val.conclusion;

            this.saveEnabled = false;
        },

        activate: function (value) {
            if (!this.order || !this.order.id) {
                this.resetOrder();

                if (!value) this.$emit("update:activate", false);
            } else {
                this.setLocalOrder(this.order);
            }
        },
    },
    created() {
        this.resetOrder();
    },

    methods: {
        clearConclusionDate() {
            this.localOrder.conclusion = null;
        },

        onStatusChange(value) {
            console.log(this.localOrder.conclusion);
            if (!this.localOrder.conclusion && value == "Final") {
                this.localOrder.conclusion = new Date(
                    Date.now() - new Date().getTimezoneOffset() * 60000
                )
                    .toISOString()
                    .substr(0, 10);
            }
        },
        close() {
            this.resetOrder();
            this.$emit("update:activate", false);
        },

        formatDate(value) {
            if (!value) return;

            return moment(value).format("DD/MM/YYYY");
        },
        resetOrder() {
            this.localOrder = new Order();
            return this.localOrder;
        },

        async setLocalOrder(order) {
            this.resetOrder();

            if (order.id) {
                this.localOrder.id = order.id;
            }
            this.localOrder.title = order.title;
            this.localOrder.type = order.type;
            this.localOrder.creation = order.creation;
            this.localOrder.conclusion = order.conclusion;
            this.localOrder.protocol = order.protocol;
            this.localOrder.status = order.status;
            this.localOrder.payed = order.payed;
            this.localOrder.customer = order.customer;
            this.localOrder.billingStatus =
                order.billingStatus == undefined
                    ? "Aberto"
                    : order.billingStatus;
            this.localOrder.billingStatusToShow = order.billingStatus  + (order.invoiceUid ? " - " + order.invoiceUid : "");
            this.localOrder.price = order.price;
            this.customer.name = order.customerName;

            if (!this.customer || !this.customer.name) {
                let customer = await this.getCustomer(order.customer.id);
                this.customer = customer;
                order.customerName = customer.name;
            }
        },

        add(orderDTO) {
            firebase
                .firestore()
                .collection("order")
                .add(orderDTO)

                .then(function (docRef) {
                    console.log("Document written with ID: ", docRef.id);
                })

                .catch(function (error) {
                    console.error("Error adding document: ", error);
                });
        },

        update(orderDTO) {
            
            firebase
                .firestore()
                .collection("order")
                .doc(this.order.id)
                .set(orderDTO, { merge: true });
        },

        save() {
            
            if (this.customer && this.customer.id) {
                this.localOrder.customer = firebase
                    .firestore()
                    .doc("/customer/" + this.customer.id);
            }
            const validationProblems = this.localOrder.validateInsert();
            if(validationProblems.length>0){
                this.alertText = 'Erro ao salvar: ';
                validationProblems.forEach((problem)=>{
                    this.alertText+= problem.msg + '; ';
                    
                });
                this.snackAlert = true;
                return;
            }

            let orderDTO = this.localOrder.getData();

            if (
                this.localOrder.id == undefined ||
                !this.localOrder.id ||
                this.localOrder.id.trim().length == 0
            ) {
                this.add(orderDTO);
            } else {
                this.update(orderDTO);
            }
            this.customer = Object();
            this.$emit("update:activate", false);
        },
        async getCustomer(customerId) {
            const customerSerice = new CustomerService();
            let customer = await customerSerice.get(customerId);

            return customer;
        },
    },
};
</script>