<template>
  <div>
    <v-dialog fullscreen max-width="800px" v-model="activate">
      <v-card>
        <v-card-title>
          <span class="headline">Clientes</span>
        </v-card-title>

        <v-divider></v-divider>

        <v-card-text>
          <v-container>
            <v-btn fab color="red accent-1" right absolute @click="add()">
              <v-icon>mdi-plus</v-icon>
            </v-btn>

            <v-data-table v-model="selectedCustomer" v-bind:headers="headers" :items="customers" show-select
              :single-select="true" class="elevation-1" item-key="id" :header-props="{ sortByText: 'Ordenar por' }">
              <template slot="items" slot-scope="props">
                <td>{{ props.item.name }}</td>
                <td class="text-xs-right">{{ props.item.phone }}</td>
                <td class="text-xs-right">{{ props.item.email }}</td>
              </template>

              <template v-slot:item.actions="{ item }">
                <v-icon small @click="edit(item)" class="mr-2">
                  mdi-pencil
                </v-icon>
              </template>
            </v-data-table>

            <customer v-bind:customer="item" v-bind:activate.sync="showCustomer"></customer>
          </v-container>
        </v-card-text>

        <v-card-actions>

          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="ok">OK</v-btn>
          <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import firebase from "firebase";
import customer from "./Customer.vue";

export default {
  name: "ListCustomers",
  components: {
    customer,
  },
  props: {
    customer: Object,
    activate: Boolean,
  },
  data() {
    return {
      showCustomer: false,
      headers: [
        { text: "Nome", value: "name", align: "left" },
        { text: "Telefone", value: "phone", align: "left" },
        { text: "E-mail", value: "email", align: "left" },
        { text: "Ações", value: "actions", sortable: false },
      ],
      customers: [],
      item: {},
      selectedCustomer: [],
    };
  },

  created() {
    this.loadCustomers();
  },

  methods: {
    loadCustomers() {
      var query = firebase.firestore().collection("customer");

      query.onSnapshot((snap) => {
        this.customers = [];
        snap.forEach((doc) => {
          let cust = doc.data();
          cust.id = doc.id;
          this.customers.push(cust);
        });
      });
    },
    close() {
      this.$emit("update:activate", false);
    },

    ok() {
      this.$emit("update:customer", this.selectedCustomer[0]);
      this.selectedCustomer = [];
      this.close();
    },

    edit(item) {
      this.item = item;
      this.showCustomer = true;
    },

    add() {
      this.item = new Object();
      this.showCustomer = true;
    },
  },
};
</script>