class Order {
    id = null;
    price = 0;
    payed = 0;
    billing = 0;
    type = "";
    creation = "";
    conclusion = "";
    protocol = "";
    status = "";
    customer = "";
    billingStatus = "";
    title = "";
    constructor() {
        this.type = 'Constituição';
        this.creation = new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
            .toISOString()
            .substr(0, 10);
        this.conclusion = null;
        this.protocol = '';
        this.status = 'Aberto';
        this.price = 0;
        this.payed = 0;
        this.customer = '';
        this.billingStatus = 'Aberto';
        this.title = "";
    }

    getData() {
        return this.removeUndefined({
            title: this.title,
            price: this.price,
            payed: this.payed,
            type: this.type,
            creation: this.creation,
            conclusion: this.conclusion,
            protocol: this.protocol,
            status: this.status,
            customer: this.customer,
            billingStatus: this.billingStatus,
            id: this.id
        });
    }
    removeUndefined(ab) {
        var keys = Object.keys(ab);

        for (var i = 0; i < keys.length; i++) {
            if (keys[i] != 'customer') {
                const value = ab[keys[i]];
                ab[keys[i]] = (value === undefined ? null : value);
            }
        }
        return ab;

    }

    validateInsert(){
        const problems = [];
        const data = this.getData();
        if(!data.customer || data.customer.length < 1){
            problems.push({field:'customer', msg: 'Campo cliente é obrigatório'});
        }
        if(!data.title || data.title.length < 1){
            problems.push({field:'title', msg: 'Campo Nome do Processo é obrigatório'});
        }
        if(!data.price || data.price < 1){
            problems.push({field:'price', msg: 'Campo preço deve ser informado'});
        }
        return problems;
    }
}
module.exports = Order;